<template>
  <div class="box">
    <van-nav-bar
    title="商品评价"
    left-text="返回"
    left-arrow
    @click-left="$router.go(-1)"
  />
    <div class="comment-list">
      <div class="comment-item" v-for="item in commentList" :key="item.content_id">
        <div class="top">
          <img :src="defaultImg" alt="">
          <div class="name">{{'hhh'}}</div>
          <van-rate :size="16" :value="item.sorce / 2" color="#ffd21e" void-icon="star" void-color="#eee"/>
        </div>
        <div class="content">
          {{ item.content }}
        </div>
        <div class="time">
          {{ item.content_time}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCommentsList } from '@/api/product'
import defaultImg from '@/assets/default-avatar.png'

export default {
  name: 'commentsIndex',
  data () {
    return {
      commentList: [],
      defaultImg,
      page: 1
    }
  },
  created () {
    this.getCommentsList()
  },
  methods: {
    async getCommentsList () {
      const { data: { list } } = await getCommentsList(this.goodsId, this.page)
      this.commentList = list.data
    }
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    }
  }
}
</script>

<style lang="less" scoped>
.comment-list {
  padding: 20px;
}
.comment {
    padding: 10px;
  }
  .comment-title {
    display: flex;
    justify-content: space-between;
    .right {
      color: #959595;
    }
  }
 .comment-item {
    font-size: 16px;
    line-height: 30px;
    border-bottom: 2px solid #ccc;
    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;
      img {
        width: 20px;
        height: 20px;
      }
      .name {
        margin: 0 10px;
      }
    }
    .time {
      color: #999;
    }
  }
</style>
